<template>
  <div class="order">
    <div class="count_top header">
      <img src="../../assets/shopImage/left_icon.png" alt="" @click="back">
      <span>待发货</span>
    </div>
    <header>
      <p class="order_title">
        <span>待发货</span>
      </p>
      <p class="order_font order_m">订单编号: {{orderInfo.order_sn}}</p>
      <p class="order_font">下单时间: {{orderInfo.create_time | time}}</p>
    </header>
    <div class="order_info" @click="seeAddress">
      <p class="order_address"><span>{{address.username}}</span> <span>{{address.telphone}}</span></p>
      <section>
        <p>
            <span v-for="list in address.city" style="font-size: 0.28rem">
              {{list.name}}
            </span>
        </p>
        <span> > </span>
      </section>
    </div>
    <div class="order_good" v-for="item in orderInfo.detail">
      <section>
        <img :src="item.pic_url" alt="">
      </section>
      <section>
        <p class="good_name">{{item.good_name}}</p>
        <div class="good_num">
          <p class="good_price">¥ {{item.good_price}}</p>
          <p>x {{item.good_num}}</p>
        </div>
      </section>
    </div>
    <div class="good_way">
      <span v-if="orderInfo.order_type === 2 || orderInfo.order_type === 3">麦粒: <strong>{{orderInfo.jifen}}</strong></span>
      <span v-if="orderInfo.order_type !== 2">实付金额: <strong>¥ {{orderInfo.shopmoney}}</strong></span>
    </div>
    <p class="leave">
      <span>买家留言</span>
      <span>{{orderInfo.content}}</span>
    </p>
    <p class="leave" style="display:flex;justify-content: space-between">
      <span>运费</span>
      <span>¥{{yunfei}}</span>
    </p>
  </div>
</template>
<script>
  import { Indicator,MessageBox} from 'mint-ui';
  import countDown from '../common/timeCountDown'
  export default {
    data(){
      return {
        id:'',
        orderInfo: {},
        address:{},
        yunfei:0,
        callback(){
          console.log('结束了')
        }
      }
    },
    methods:{
      back(){
        this.$router.go(-1);
      },
      seeAddress(){
        // this.$router.push('/site')
      },
      getOrderInfo(id){
        this.post('home/Order/getOrderAllInfo', {
          order_id: id
        }).then(res => {
          if (res.data.code === 10000){
            this.orderInfo = res.data.info;
            this.address = res.data.info.address;
            this.address.city = JSON.parse(res.data.info.address.city);
            this.orderInfo.detail.forEach(item => {
              item.pic_url = this.imageURL(item.pic_url);
              this.yunfei += (item.good_yunfei - 0)
            })
          }
        })
      },
    },
    created(){
      this.id = this.$route.params.id;
      this.getOrderInfo(this.id);
    },
    mounted(){

    },
    components: {
      countDown
    }
  }
</script>
<style scoped>
  @import "../../../static/order.css";
  @import "../../../static/header.css";
</style>



